html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.125rem;
  margin: 0;
  padding: 0;
  background-color: var(--color-bg);
  font-family: var(--font);
  color: var(--color-body);
}

@media (min-width: 47.75em) {
  .hide__for__pc {
    display: none;
  }
}

@media (max-width: 47.74875em) {
  .collapsed {
    display: none;
  }
}

.highlight {
  color: var(--color-accent);
}

.line {
  display: block;
  border-right: 1px solid #C0C5D2;
}
.line--accent {
  width: 100%;
  border-bottom: 2px solid var(--color-accent);
}
@media (min-width: 47.75em) {
  .line--accent {
    border-bottom: 0;
    border-right: 2px solid var(--color-accent);
  }
}
@media (max-width: 47.74875em) {
  .line {
    display: none;
  }
}

h1, h2, h3 {
  margin-top: 0;
  color: var(--color-headings);
}

h1 {
  font-size: 2.25rem;
  font-weight: 800;
  color: var(--color-headings);
}
@media (min-width: 47.75em) {
  h1 {
    font-size: 3.5rem;
  }
}

h2 {
  font-size: 1.5rem;
  font-weight: 800;
}
@media (min-width: 47.75em) {
  h2 {
    font-size: 3.25rem;
  }
}

h3 {
  font-size: 1.125rem;
}
@media (min-width: 47.75em) {
  h3 {
    font-size: 1.75rem;
  }
}

p {
  margin-top: 0;
  font-size: 0.75rem;
  color: var(--color-body);
}
@media (min-width: 47.75em) {
  p {
    font-size: 1.25rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font: "Manrope", sans-serif;
}

:root {
  --color-body: #5B5B5B;
  --color-headings: #2B2B2B;
  --color-accent: #FF6600;
  --color-white: #fff;
  --color-bg: #FAFAFA;
}

.container {
  width: 100%;
  max-width: 75rem;
  margin-inline: auto;
}
@media (max-width: 47.74875em) {
  .container {
    padding: 0 1rem;
  }
}

.header {
  margin-top: 2.75rem;
}

.nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (min-width: 47.75em) {
  .nav {
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
  }
}
.nav__logo {
  width: 8.75rem;
}
@media (min-width: 47.75em) {
  .nav__logo {
    width: 15rem;
  }
}
.nav__list {
  width: 100%;
  list-style: none;
  line-height: 2;
  text-align: center;
  padding-left: 0;
  gap: 2.8125rem;
}
@media (min-width: 47.75em) {
  .nav__list {
    display: flex;
    width: fit-content;
  }
}
.nav__list__item a {
  color: var(--color-headings);
}
.nav__list__item--home a {
  font-weight: bold;
  color: var(--color-accent);
}

.hero {
  margin-top: 2.75rem;
}
.hero__companies {
  display: flex;
  justify-content: center;
  gap: 1.79125rem;
  margin-bottom: 1.5rem;
}
.hero__companies img {
  max-height: 0.951875rem;
}
@media (min-width: 47.75em) {
  .hero__companies img {
    max-height: 2.5rem;
  }
}

.hero-content {
  text-align: center;
}
@media (min-width: 47.75em) {
  .hero-content {
    text-align: left;
    margin-top: 3.125rem;
    margin-bottom: 10rem;
  }
}
.hero-content__heading {
  margin-bottom: 1.25rem;
}
@media (min-width: 47.75em) {
  .hero-content__heading {
    margin-bottom: 2.25rem;
  }
}
.hero-content__text {
  font-size: 0.875rem;
  margin-bottom: 1.875rem;
}
@media (min-width: 47.75em) {
  .hero-content__text {
    margin-bottom: 4.625rem;
    max-width: 25rem;
    font-size: 1.125rem;
  }
}
.hero-content__img {
  width: 100%;
  margin-bottom: 3rem;
}
@media (min-width: 47.75em) {
  .hero-content__img {
    transform: translateY(-3.125rem);
  }
}
.hero-content__btns {
  display: flex;
  gap: 1.25rem;
  justify-content: center;
  margin-bottom: 3rem;
}
@media (min-width: 47.75em) {
  .hero-content__btns {
    justify-content: start;
    margin-bottom: 0;
  }
}

.btn {
  font-family: var(--font);
  border-radius: 8px;
  font-weight: 600;
  width: 100%;
  max-width: 10rem;
  padding: 1rem 1rem;
  border: 1px solid var(--color-accent);
  cursor: pointer;
  font-size: 0.875rem;
}
@media (min-width: 47.75em) {
  .btn {
    font-size: 1rem;
  }
}
.btn__arrow {
  margin-left: 0.5rem;
  transform: translateY(1px);
}
.btn--nav {
  margin-top: 1.25rem;
  margin-inline: auto;
}
.btn--primary {
  background: var(--color-accent);
  color: white;
}
.btn--secondary {
  background: var(--color-white);
  color: black;
}

.grid {
  display: grid;
}
.grid--cols-2 {
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 47.74875em) {
  .grid--cols-2 {
    grid-template-columns: 1fr;
  }
}
.grid--header {
  align-items: center;
}
.grid--cols-3 {
  grid-template-columns: 1fr;
}
@media (min-width: 47.75em) {
  .grid--cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

.footer {
  background: var(--color-headings);
  display: flex;
  justify-content: space-between;
}
.footer__image {
  width: 15.625rem;
  position: absolute;
}
.footer__image--right {
  transform: translateX(21.875rem);
}
.footer-content {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  text-align: center;
  width: 100%;
}
.footer-content__logo {
  margin-top: 2.1875rem;
}
@media (min-width: 47.75em) {
  .footer-content__logo {
    margin-top: 3.5rem;
  }
}
.footer-content__list {
  list-style: none;
  padding-left: 0;
  font-size: 0.75rem;
  display: flex;
  gap: 3.125rem;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 3.4375rem 5.1875rem;
}
@media (min-width: 47.75em) {
  .footer-content__list {
    margin-bottom: 2.5rem;
    font-size: 1.125rem;
  }
}
.footer-content__list__item a {
  color: white;
}
.footer-content__icons {
  margin: 2.5rem 0;
  display: flex;
  gap: 1.5rem;
}

.icon {
  width: 2.25rem;
  height: 2.25rem;
}

.about {
  margin-top: 3.125rem;
  margin-bottom: 3rem;
}
@media (min-width: 47.75em) {
  .about {
    margin-top: 8.75rem;
    margin-bottom: 10rem;
  }
}
.about__image {
  width: 100%;
}
.about__content {
  text-align: center;
}
@media (min-width: 47.75em) {
  .about__content {
    text-align: left;
  }
}
.about__heading {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}
.about__text {
  line-height: 180%;
  margin-bottom: 1rem;
}
@media (min-width: 47.75em) {
  .about__text {
    margin-bottom: 2.1875rem;
  }
}
.about__statistics {
  display: grid;
  flex-direction: column;
  float: left;
  width: 100%;
  justify-content: center;
}
@media (min-width: 47.75em) {
  .about__statistics {
    grid-template-columns: repeat(3, 1fr);
  }
}
.about__container {
  margin: 1rem auto;
  width: 100%;
  float: left;
}

.item-about {
  display: flex;
  align-items: center;
  text-align: center;
}
@media (min-width: 47.75em) {
  .item-about {
    flex-direction: column;
  }
}
.item-about__container {
  padding: 0.625rem;
  background: #FFEDE6;
  margin-right: 1.875rem;
  width: 4rem;
  height: 2.8125rem;
  border-radius: 6px;
}
@media (min-width: 47.75em) {
  .item-about__container {
    margin-right: 0;
    padding: 0;
    background: none;
    margin-bottom: 0.5rem;
    width: fit-content;
  }
}
.item-about__num {
  font-size: 1.125rem;
  color: var(--color-accent);
  font-weight: bold;
}
@media (min-width: 47.75em) {
  .item-about__num {
    font-size: 2.5rem;
  }
}
.item-about__text {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0;
}
@media (min-width: 47.75em) {
  .item-about__text {
    font-size: 1.5rem;
  }
}

.interests {
  text-align: center;
}
.interests__content {
  gap: 1.5rem;
}
@media (min-width: 47.75em) {
  .interests__content {
    margin-top: 5rem;
  }
}
.interests__heading {
  margin-bottom: 1rem;
}
.interests__text {
  line-height: 180%;
  max-width: 47.8125rem;
  margin-inline: auto;
}

.item-interests {
  text-align: left;
  display: grid;
  grid-template-columns: min-content auto;
  margin: 1.5rem 1.5rem 1.5rem;
}
@media (min-width: 47.75em) {
  .item-interests {
    display: inline-block;
  }
}
.item-interests__container {
  width: 3rem;
  height: 3rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  margin-right: 1rem;
}
@media (min-width: 47.75em) {
  .item-interests__container {
    margin-bottom: 1.5rem;
    width: 4.375rem;
    height: 4.375rem;
  }
}
.item-interests__container--yellow {
  background: #FFEDE6;
}
.item-interests__container--pink {
  background: #FFE1E9;
}
.item-interests__container--green {
  background: #DBF9ED;
}
.item-interests__icon {
  width: 1.375rem;
  height: 1.375rem;
}
@media (min-width: 47.75em) {
  .item-interests__icon {
    width: 2rem;
    height: 2rem;
  }
}
.item-interests__title {
  margin-bottom: 0.75rem;
}
@media (min-width: 47.75em) {
  .item-interests__title {
    margin-bottom: 1.5rem;
  }
}
.item-interests__text {
  margin-bottom: 0.5rem;
  line-height: 180%;
}
@media (min-width: 47.75em) {
  .item-interests__text {
    font-size: 1.125rem;
    margin-bottom: 2rem;
  }
}
.item-interests__link {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--color-accent);
}
@media (min-width: 47.75em) {
  .item-interests__link {
    font-size: 1.125rem;
  }
}

.ourvalues {
  margin-top: 3rem;
}
.ourvalues__image {
  width: 100%;
}
.ourvalues__content {
  text-align: center;
}
@media (min-width: 47.75em) {
  .ourvalues__content {
    text-align: left;
    margin-left: 4.375rem;
  }
}
.ourvalues__heading {
  margin: 1rem 0;
}
@media (min-width: 47.75em) {
  .ourvalues__heading {
    margin-bottom: 2.1875rem;
  }
}
.ourvalues__text {
  line-height: 180%;
  margin-bottom: 2rem;
}
@media (min-width: 47.75em) {
  .ourvalues__text {
    margin-bottom: 2.1875rem;
  }
}

.currentpj {
  margin-top: 2.9375rem;
}
@media (min-width: 47.75em) {
  .currentpj {
    margin-bottom: 5.625rem;
  }
}
.currentpj__heading {
  text-align: center;
  margin-bottom: 1.5rem;
}
@media (min-width: 47.75em) {
  .currentpj__heading {
    margin: 11.25rem 0 5.25rem;
  }
}
.currentpj__content {
  gap: 1.875rem;
}

.item-currentpj {
  margin-bottom: 2.5rem;
}
.item-currentpj__image {
  width: 100%;
  height: 16.875rem;
  object-fit: cover;
}
@media (min-width: 47.75em) {
  .item-currentpj__image {
    height: 23.75rem;
  }
}
.item-currentpj__title {
  margin: 1rem 0;
  max-width: 16.25rem;
  font-size: 1.5rem;
}
@media (min-width: 47.75em) {
  .item-currentpj__title {
    font-size: 1.75rem;
    max-width: 18.75rem;
  }
}
.item-currentpj__text {
  margin: 1rem 0;
  line-height: 180%;
}
.item-currentpj__link {
  font-size: 1rem;
  color: var(--color-accent);
  font-weight: 600;
}
@media (min-width: 47.75em) {
  .item-currentpj__link {
    font-size: 1.25rem;
  }
}